}

/* Icon circle styling with pulsing effect */
.icon-circle {
  width: 40px; /* Smaller */
  height: 40px; /* Smaller */
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.1rem; /* Smaller */
  color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.icon-circle::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: white;
  border-radius: 50%;
  opacity: 0;
  transform: scale(0);
  transition: transform 0.5s, opacity 0.3s;
}

.hover-card:hover .icon-circle::after {
  opacity: 0.2;
  transform: scale(1.5);
}

