.ecg-path {
  stroke: #0891b2;
  stroke-dasharray: 400;
  stroke-dashoffset: 400;
  animation: dash 3s linear infinite;
}

@keyframes dash {
  from {
    stroke-dashoffset: 400;
  }
  to {
    stroke-dashoffset: 0;
  }
}

.spinner-text {
  color: #0891b2;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 1px;
}

@keyframes spinner-rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(0.95);
    opacity: 0.8;
  }
  50% {
    transform: scale(1);
    opacity: 1;
  }
}

/* Responsive styles */
@media (max-width: 768px) {
  .spinner-container {
    width: 70px;
    height: 70px;
  }
  
  .spinner-ring {
    width: 70px;
    height: 70px;
  }
  
  .heartbeat-container {
    width: 120px;
  }
  
  .heartbeat-line {
    width: 120px;
  }
}

/* Support for dark mode */
@media (prefers-color-scheme: dark) {
  .spinner-overlay {
    background-color: rgba(15, 23, 42, 0.9);
  }
  
  .spinner-ring {
    border: 4px solid #1e293b;
    border-top: 4px solid #38bdf8;
  }
  
  .cross-horizontal, .cross-vertical {
    background-color: #38bdf8;
  }
  
  .ecg-path {
    stroke: #38bdf8;
  }
  
  .spinner-text {
    color: #38bdf8;
  }
}
:root {
  --primary-color: #2563eb;  /* Blue 600 */
  --secondary-color: #3b82f6; /* Blue 500 */
  --accent-color: #f97316;   /* Orange 500 */
  --success-color: #22c55e;  /* Green 500 */
  --warning-color: #f59e0b;  /* Amber 500 */
  --danger-color: #ef4444;   /* Red 500 */
  --background-color: #ffffff; /* White background */
  --card-bg: #ffffff;
  --card-shadow: rgba(0, 0, 0, 0.1);
  --text-primary: #1e293b;   /* Slate 800 */
  --text-secondary: #64748b; /* Slate 500 */
  --border-color: #e2e8f0;   /* Slate 200 */
  --border-radius: 0.5rem;
  --card-border-radius: 12px;
  --input-border-radius: 8px;
  --button-border-radius: 10px;
}

/* Dark mode variables - using more subtle colors */
@media (prefers-color-scheme: dark) {
  :root {
    --primary-color: #3b82f6;    /* Blue 500 */
    --secondary-color: #60a5fa;  /* Blue 400 */
    --background-color: #f8fafc; /* Very light gray instead of black */
    --card-bg: #ffffff68;
    --card-shadow: rgba(0, 0, 0, 0.15);
    --text-primary: #0c1017;    /* Slate 800 */
    --text-secondary: #000000;  /* Slate 500 */
    --border-color : #e2e8f0;    /* Slate 200 */
  }
}

body {
  background-color: #f8fafc;
  background-image: 
    radial-gradient(at 90% 5%, hsla(216, 91%, 90%, 0.2) 0px, transparent 50%),
    radial-gradient(at 10% 90%, hsla(240, 82%, 90%, 0.2) 0px, transparent 50%);
  background-attachment: fixed;
  background-size: cover;
}

/* Page title styling */
.page-title {
  color: var(--primary-color);
  margin-bottom: 2rem;
  font-weight: 700;
  text-align: center;
  font-size: 2.2rem;
  position: relative;
  padding-bottom: 1rem;
  display: inline-block;
  left: 50%;
  transform: translateX(-50%);
}

.page-title::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 25%;
  width: 50%;
  height: 4px;
  background: linear-gradient(90deg, var(--primary-color), var(--secondary-color));
  border-radius: 2px;
}

.page-title .icon {
  margin-right: 0.5rem;
  color: var(--primary-color);
  filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.1));
}

/* Add/Edit form card styling with glassmorphism */
.vaccine-form-card {
  background: var(--card-bg);
  border-radius: var(--card-border-radius);
  padding: 30px;
  box-shadow: 0 8px 32px var(--card-shadow);
  margin-bottom: 40px;
  border: 1px solid var(--border-color);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.vaccine-form-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 6px;
  background: linear-gradient(90deg, var(--primary-color), var(--secondary-color));
}

.form-title {
  color: var(--primary-color);
  margin-bottom: 25px;
  font-weight: 700;
  padding-bottom: 15px;
  border-bottom: 1px solid var(--border-color);
  display: flex;
  align-items: center;
  font-size: 1.5rem;
}

.form-section {
  margin-bottom: 30px;
  padding-bottom: 20px;
  position: relative;
}

.form-section-title {
  color: var(--text-primary);
  font-size: 1.2rem;
  margin-bottom: 20px;
  font-weight: 600;
  position: relative;
  padding-left: 15px;
  display: flex;
  align-items: center;
}

.form-section-title::before {
  content: '';
  position: absolute;
  left: 0;
  top: 10%;
  height: 80%;
  width: 4px;
  background: linear-gradient(180deg, var(--primary-color), var(--secondary-color));
  border-radius: 2px;
}

.form-text {
  font-size: 0.8rem;
  color: var(--text-secondary);
  margin-top: 5px;
  transition: all 0.2s ease;
}

.form-control {
  border-radius: 8px;
  border: 1px solid var(--border-color);
  padding: 12px 16px;
  color: var(--text-primary);
  background-color: var(--card-bg);
  transition: all 0.2s ease;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.05);
}

.form-control::placeholder {
  color: var(--text-muted);
  opacity: 0.7;
}

.form-control:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(67, 97, 238, 0.15);
  transform: translateY(-1px);
}

.form-control:focus + .form-text {
  color: var(--primary-color);
}

.form-actions {
  display: flex;
  padding-top: 20px;
  border-top: 1px solid var(--border-color);
}

.form-label {
  color: var(--text-primary);
  font-weight: 600;
  margin-bottom: 8px;
  display: block;
  font-size: 0.95rem;
}

.form-label .text-danger {
  font-size: 1.2em;
  line-height: 0;
  position: relative;
  top: 3px;
}

/* Button styling with animations */
.btn {
  padding: 10px 20px;
  border-radius: var(--button-border-radius);
  font-weight: 600;
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  z-index: 1;
}

.btn::after {
  content: '';
  position: absolute;
  width: 0%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.1);
  transition: width 0.3s ease;
  z-index: -1;
}

.btn:hover::after {
  width: 100%;
}

.btn:active {
  transform: translateY(2px);
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1);
}

.btn-primary {
  background: linear-gradient(to right, var(--primary-color), var(--secondary-color));
  color: white;
}

.btn-primary:hover {
  background: linear-gradient(to right, var(--secondary-color), var(--primary-color));
}

.btn-secondary {
  background: linear-gradient(to right, var(--text-secondary), #4b5563);
  color: white;
}

.btn-lg {
  padding: 14px 28px;
  font-size: 1rem;
  border-radius: 12px;
}

/* Vaccine cards with modern design */
.vaccination-cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  grid-gap: 24px;
  gap: 24px;
  margin-bottom: 30px;
}

.vaccine-card {
  background-color: var(--card-bg);
  border-radius: var(--card-border-radius);
  box-shadow: 0 4px 10px var(--card-shadow);
  transition: all 0.3s ease;
  margin-bottom: 1.25rem;
  overflow: hidden;
  border: 1px solid var(--border-color);
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.vaccine-card:hover {
  box-shadow: 0 10px 15px var(--card-shadow);
  transform: translateY(-5px);
}

.vaccine-card h4 {
  color: var(--primary-color);
  margin-bottom: 15px;
  font-weight: 600;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
}

.vaccine-card-info {
  margin-bottom: 15px;
}

.vaccine-card-info p {
  margin-bottom: 8px;
  display: flex;
  align-items: flex-start;
}

.vaccine-card-info p strong {
  color: var(--text-primary);
  margin-right: 5px;
  display: flex;
  align-items: center;
}

.vaccine-card-actions {
  margin-top: auto;
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  justify-content: flex-end;
}

/* Status badges */
.status-badge {
  display: inline-flex;
  align-items: center;
  padding: 0.35rem 0.75rem;
  border-radius: 30px;
  font-size: 0.875rem;
  font-weight: 500;
  margin-left: 8px;
}

.status-badge svg {
  margin-right: 0.4rem;
}

.status-badge.upcoming {
  background-color: rgba(46, 204, 113, 0.15);
  color: var(--success-color);
}

.status-badge.status-overdue {
  background-color: rgba(231, 76, 60, 0.15);
  color: var(--danger-color);
}

.status-badge.status-due-soon {
  background-color: rgba(243, 156, 18, 0.15);
  color: var(--warning-color);
}

.status-badge.status-completed {
  background-color: rgba(67, 97, 238, 0.15);
  color: var(--primary-color);
}

/* Action buttons */
.action-buttons {
  display: flex;
  justify-content: flex-end;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  margin-top: 1rem;
}

.action-buttons .btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.4rem 0.75rem;
  border-radius: 0.4rem;
  transition: all 0.3s ease;
}

.action-buttons .btn svg {
  margin-right: 0.4rem;
}

.btn-edit {
  background-color: rgba(67, 97, 238, 0.1);
  color: var(--primary-color);
  border: none;
}

.btn-edit:hover {
  background-color: var(--primary-color);
  color: white;
}

.btn-delete {
  background-color: rgba(231, 76, 60, 0.1);
  color: var(--danger-color);
  border: none;
}

.btn-delete:hover {
  background-color: var(--danger-color);
  color: white;
}

/* Form Styling */
.vaccination-modal .modal-content {
  border-radius: var(--card-border-radius);
  border: none;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.vaccination-modal .modal-header {
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.vaccination-modal .modal-title {
  display: flex;
  align-items: center;
  color: var(--primary-color);
  font-weight: 600;
}

/* Loading spinner */
.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 200px;
}

/* Styled Spinner matching the app's theme */
.spinner-border {
  color: var(--primary-color);
  width: 3rem;
  height: 3rem;
  border-width: 0.25em;
  animation: spinner-border 0.75s linear infinite;
}

.text-center .spinner-border {
  margin: 2rem auto;
}

/* Notification */
.alert {
  border-radius: var(--border-radius);
  padding: 1rem;
  margin-bottom: 1.5rem;
  border: none;
  box-shadow: var(--box-shadow);
}

/* Empty state */
.empty-state {
  text-align: center;
  padding: 3rem 1rem;
  background-color: var(--card-bg);
  border-radius: var(--border-radius);
  box-shadow: 0 4px 10px var(--card-shadow);
}

.empty-state svg {
  font-size: 3rem;
  color: var(--secondary-color);
  margin-bottom: 1rem;
}

.empty-state h3 {
  margin-bottom: 1rem;
  color: var(--text-primary);
}

.empty-state p {
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
}

/* Tabs styling to match MedicationTracker */
.tabs-container {
  margin-bottom: 30px;
}

.nav-tabs-wrapper {
  margin-bottom: 20px;
  border-bottom: 1px solid var(--border-color);
}

.nav-tabs {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav-item {
  margin-bottom: -1px;
}

.nav-link {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  border: 1px solid transparent;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  color: var(--text-secondary);
  background-color: transparent;
  cursor: pointer;
  transition: all 0.2s ease;
  font-weight: 500;
  text-decoration: none;
}

.nav-link:hover {
  color: var(--primary-color);
  border-color: var(--border-color) var(--border-color) transparent;
}

.nav-link.active {
  color: var(--primary-color);
  background-color: var(--card-bg);
  border-color: var(--border-color) var(--border-color) var(--card-bg);
  font-weight: 600;
}

.tab-content {
  padding: 20px;
  background-color: var(--card-bg);
  border-radius: 0 0 8px 8px;
  box-shadow: 0 2px 8px var(--card-shadow);
}

.tab-pane {
  display: none;
}

.tab-pane.active {
  display: block;
}

.upcoming-badge {
  background-color: var(--danger-color);
  color: white;
  font-size: 0.7rem;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
}

/* Upcoming section styling */
.upcoming-section {
  background: var(--card-bg);
  border-radius: var(--card-border-radius);
  margin-bottom: 30px;
  overflow: hidden;
  box-shadow: 0 4px 10px var(--card-shadow);
  border: 1px solid var(--border-color);
}

.upcoming-header {
  background: rgba(67, 97, 238, 0.08);
  padding: 15px 20px;
  border-bottom: 1px solid rgba(67, 97, 238, 0.15);
}

.upcoming-header h3 {
  color: var(--primary-color);
  font-weight: 600;
  display: flex;
  align-items: center;
  font-size: 1.2rem;
}

.upcoming-body {
  padding: 15px 20px;
}

.upcoming-body .table {
  margin-bottom: 0;
}

.section-title {
  color: var(--primary-color);
  margin-bottom: 20px;
  font-weight: 600;
  position: relative;
  display: inline-block;
  padding-bottom: 8px;
}

.section-title::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background: linear-gradient(90deg, var(--primary-color), transparent);
  border-radius: 1.5px;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .vaccination-cards {
    grid-template-columns: 1fr;
  }
  
  .vaccine-form-card {
    padding: 20px;
  }
  
  .form-actions {
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
  }
  
  .form-actions .btn {
    width: 100%;
  }
  
  .form-section-title {
    font-size: 1.1rem;
  }
  
  .vaccination-info {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .vaccination-info svg {
    margin-bottom: 0.5rem;
  }
  
  .action-buttons {
    flex-direction: column;
  }
  
  .action-buttons .btn {
    width: 100%;
    margin-bottom: 0.5rem;
  }

  .page-title, .section-title {
    font-size: 1.6rem;
  }
  
  .nav-link {
