    color: var(--text-color);
  }
  
  .form-control:focus {
    background-color: #1a1d29;
    color: var(--text-color);
  }
  
  .empty-state {
    background-color: #2a2d3a;
    color: var(--text-color);
  }
  
  .vaccination-modal .modal-content {
    background-color: #2a2d3a;
  }
  
  .tab-content {
