/* Medication Tracker Styles - Using global theme variables */

/* Medication Cards */
.medication-card {
  background: var(--card-bg);
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 4px 10px var(--card-shadow);
  height: 100%;
  display: flex;
  flex-direction: column;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  border: 1px solid var(--border-color);
  min-height: 400px;
}

.medication-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 15px var(--card-shadow);
}

.medication-card h4 {
  color: var(--primary);
  margin-bottom: 15px;
  font-weight: 600;
  font-size: 1.2rem;
}

.medication-card p {
  margin-bottom: 8px;
  color: var(--text-secondary);
  font-size: 0.95rem;
}

.medication-card p strong {
  color: var(--text-primary);
}

.medication-card .text-danger {
  color: var(--danger);
  font-weight: 500;
}

.medication-card .btn {
  padding: 6px 12px;
  font-size: 0.85rem;
  border-radius: 8px;
  transition: all 0.2s ease;
  white-space: nowrap;
}

.medication-card .btn-group {
  margin-top: auto;
  padding-top: 15px;
  display: flex;
  flex-wrap: wrap;
  grid-gap: 8px;
  gap: 8px;
  justify-content: space-between;
}

/* History Section */
.history-section {
  background: var(--card-bg);
  border-radius: 12px;
  padding: 20px;
  margin-top: 20px;
  box-shadow: 0 4px 10px var(--card-shadow);
  border: 1px solid var(--border-color);
}

.history-section h3 {
  color: var(--primary);
  margin-bottom: 20px;
}

.history-table {
  width: 100%;
  border-collapse: collapse;
}

.history-table th {
  background-color: rgba(67, 97, 238, 0.1);
  color: var(--primary);
  font-weight: 600;
  padding: 12px;
  text-align: left;
  border-bottom: 2px solid var(--primary);
}

.history-table td {
  padding: 12px;
  border-bottom: 1px solid var(--border-color);
  color: var(--text-secondary);
}

.history-table tr:hover {
  background-color: rgba(67, 97, 238, 0.05);
}

/* Status badges */
.status-badge {
  padding: 3px 6px;
  border-radius: 4px;
  font-size: 0.75rem;
  font-weight: 500;
}

.status-taken {
  background-color: var(--success);
  color: white;
}

.status-missed {
  background-color: var(--warning);
  color: white;
}

.status-skipped {
  background-color: var(--accent-orange);
  color: white;
}

/* Day buttons */
.day-button {
  padding: 5px;
  margin: 3px;
  cursor: pointer;
}

.day-button.selected {
  background-color: var(--primary);
  color: white;
}

.day-button:not(.selected) {
  background-color: var(--background-color);
  color: var(--text-primary);
  border: 1px solid var(--border-color);
}

/* Form elements */
.form-control {
  border: 1px solid var(--border-color);
  padding: 8px 12px;
  border-radius: 8px;
  color: var(--text-primary);
  background-color: var(--card-bg);
}

.form-control:focus {
  border-color: var(--primary);
  box-shadow: 0 0 0 0.2rem rgba(67, 97, 238, 0.25);
}

.form-label {
  font-weight: 500;
  display: block;
  margin-bottom: 6px;
  color: var(--text-primary);
  font-size: 0.9rem;
}

/* Page title */
.page-title {
  color: var(--primary);
  margin-bottom: 1.5rem;
  font-weight: 700;
  text-align: center;
  position: relative;
}

.page-title .icon {
  margin-right: 8px;
  color: var(--primary);
  filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.1));
}

/* Tabs Navigation styles */
.tabs-container {
  margin-bottom: 30px;
}

.nav-tabs-wrapper {
  margin-bottom: 20px;
  border-bottom: 1px solid var(--border-color);
}

.nav-tabs {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav-item {
